.welcome-component {
  width: 100%;
  text-align: center;
  padding-top: 100px;
}

@media only screen and (min-width: 992px) {
  .welcome-component {
    width: 100%;
    text-align: left;
    font-size: 1.0em;
  }
}

.welcome-component .typist-container {
  color: #fff;
  font-size: 2.0em;
}

.welcome-component .highlight {
  color: #E31B6D;
}

.welcome-component .title {
  font-family: ralewayregular, sans-serif;
  font-weight: bold;
  font-size: 1.0em !important;
  color: #fff;
  text-align: left;
}

.welcome-component .quote {
  font-family: ralewayregular, sans-serif;
  font-size: 0.7em;
  color: #ddd;
  text-align: left;
  font-weight: bold;
}

.welcome-component .button-container {
  margin: 30px 0;
}

@media only screen and (max-width: 991px) {
  .welcome-component .button-container {
    font-size: 1.2em;
  }
}