.home-section {
    background-color: #262b2e;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 900px;
    height: 100vh;
    position: relative;
    overflow: hidden;
}
.home-section .visited {
    position: absolute;
    top: 40px;
    right: 80px;
    color: #ffffff;
    font-family: "Helvetica Neue";
}
.home-section .Aligner-item {
    width: 100%;
    min-height: 1px;
}
.skills-container {
    align-self: center;
}
@media only screen and (min-width: 991px) {
    .home-section .Aligner-item {
        max-width: 50%;
    }
}
.home-section .Aligner-item--top {
    align-self: flex-start;
}
.home-section .Aligner-item--bottom {
    align-self: flex-end;
}
