.floating-photo {
  width: 200px;
  height: 200px;
  box-sizing: border-box;
  border: 5px #fff solid;
  border-radius: 50%;
  overflow: hidden;
  animation: white_float 6s ease-in-out infinite;
}

.floating-photo a {
  cursor: pointer;
}

.floating-photo a img {
  width: 100%;
  height: auto;
}

@keyframes white_float {
  0% {
    box-shadow: 0 5px 15px 0 rgba(255, 255, 255, 0.6);
    transform: translateY(0px); }
  50% {
    box-shadow: 0 25px 15px 0 rgba(255, 255, 255, 0.2);
    transform: translateY(-20px); }
  100% {
    box-shadow: 0 5px 15px 0 rgba(255, 255, 255, 0.6);
    transform: translateY(0px); } }
