.contact-section {
  position: relative;
  overflow: hidden;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 90px 0;
  background-color: #262b2e;
}

.contact-section .title {
  font-family: ralewayregular, sans-serif;
  font-size: 3em;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 0 1px #edf3f8;
}

.contact-section .title-bar {
  width: 90px;
  border-bottom: 4px solid #edf3f8;
  margin: 0 auto 70px auto;
  animation-delay: 0.15s;
  box-shadow: 0 0 1px #edf3f8;
}

.contact-section .subject {
  resize: none;
}

.contact-section input {
  margin-bottom: 5px;
}

.contact-section .hide {
  display: none;
}

.contact-section .message {
  margin-top: 10px;
}

.contact-section .message.failed {
  color: red;
}

.contact-section .submit-button {
  margin-top: 30px;
  float: right;
  color: #ffffff;
  border: 1px solid #ffffff;
  background-color: transparent;
  border-radius: 0;
  box-shadow: 0 0 1px #edf3f8;
  font-size: 1.2em;
  width: 120px;
}

.contact-section .submit-button:hover {
  background-color: #04C2C9;
  border: 1px solid #04C2C9;
  color: white;
  cursor: pointer;
  box-shadow: 0 0 1px #444649;
}
