.projects-section {
  padding: 53px 0;
  background: #edf3f8;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.projects-section:after {
  display: block;
  position: absolute;
  content: '';
  left: 50%;
  width: 30px;
  height: 30px;
  transform: translateX(-50%) rotate(45deg);
  bottom: -15px;
  z-index: 10;
  background: inherit;
  box-shadow: -30px 30px 0 #edf3f8, 30px -30px 0 #edf3f8;
}

.projects-section .title {
  font-family: ralewayregular, sans-serif;
  font-size: 3em;
  text-align: center;
  color: black;
  text-shadow: 0 0 2px #262b2e;
}

.projects-section .title-bar {
  width: 90px;
  border-bottom: 4px solid black;
  margin: 0 auto 70px auto;
  animation-delay: 0.15s;
  box-shadow: 0 0 1px #262b2e;
}
