.canvas-container {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.7;
  z-index: 0;
}

.canvas-container:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(to right, #0d071e 0%, rgba(13, 7, 30, 0.9) 30%, rgba(34, 14, 93, 0.25) 50%);
  animation-name: showUpKeyframe;
  animation-delay: 4s;
  animation-duration: 1s;
  visibility: hidden;
  opacity: 0;
  animation-fill-mode: forwards;
  z-index: 10;
}

.canvas-container .canvasBG {
  position: absolute;
  top: 0;
  left: 0;
  filter: blur(3px);
  opacity: 0.6;
}

.canvas-container .canvas {
  z-index: 1;
}

.child-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}
