.my-shuffle-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.project-detail-modal {
  background-color: #edf3f8;
}

.project-detail-modal .modal-content {
  background-color: #edf3f8;
}

.project-detail-modal .title {
  font-family: ralewaybold, sans-serif;
  font-size: 2.0em;
  color: #444;
  padding: 0 10px;
}

.project-detail-modal .sub-title {
  font-size: 1.0em;
  color: #999;
  padding: 0 10px;
}

.project-detail-modal .divider {
  height: 1px;
  width: 100%;
  margin: 10px 0 20px 0;
  background: #ddd;
  padding: 0 10px;
}

.project-detail-modal .modal-footer {
  border-color: #ddd;
}

.project-detail-modal .description {
  padding: 0 10px;
}

.project-detail-modal .visit-button {
  background: #007bff;
  color: white;
  padding: 0.375rem 0.75rem;
}

.project-detail-modal .visit-button:hover {
  text-decoration: none;
}
